import { Link } from "react-router-dom";

const ArticleLink = ({article}) => {

    return (
        article ? 
            <Link to={`/article/${article.id}`} className="article-link">
                <h2 className="article-link-title">{article.title}</h2>
                <div className="article-link-upload">{article.upload}</div>
                <div className="article-link-content">{article.content}</div>
            </Link>
        :
            <div className="article-link">
                <h2 className="article-link-title article-link-unload" />
                <div className="article-link-upload article-link-unload" />
                <div className="article-link-content article-link-unload" />
            </div>
    );
}

export default ArticleLink;