import { useEffect, useState } from "react";
import ArticleLink from "./ArticleLink";
import { Helmet } from "react-helmet-async";

const Top = () => {

    const [articleList, setArticleList] = useState([null, null, null, null]);

    useEffect(() => {
        (async () => {
            try {
                setArticleList(
                    await (
                        await fetch('/articlelist.json')
                    ).json()
                );
            } catch (e) {
                setArticleList(null);
            }
        })();
    }, []);

    return (
        <div>
            <Helmet>
                <title> | LOGGIT</title>
            </Helmet>
            <h1></h1>
            {articleList ?
                articleList.map((article, key) => 
                    <ArticleLink key={key} article={article} />
                )
            : ''}
        </div>
    );
}

export default Top;