import { useEffect, useState } from "react";
import { useParams } from "react-router-dom"
import ArticleDetail from "./ArticleDetail";
import { Helmet } from "react-helmet-async";
const Article = () => {

    const { id } = useParams();
    const [title, setTitle] = useState(null);
    const [upload, setUpload] = useState(null);
    const [articleData, setArticleData] = useState(null);
    const [notFound, setNotFound] = useState(false);

    useEffect(() => {
        if (title) setTitle(null);
        if (upload) setUpload(null);
        setNotFound(false);
        if (!isNaN(id)) {
            (async () => {
                try {
                    const articles = await(await fetch('/articlelist.json')).json();
                    const filtered = articles.filter(article => article.id === Number(id));
                    if (filtered.length === 0) {
                        setNotFound(true);
                        return;
                    }
                    const article = filtered[0];
                    setTitle(article.title);
                    setUpload(article.upload);
                    const articleText = await (await fetch(`/article/${id}.atl`)).text();
                    setArticleData(articleText);
                } catch (e) {
                    setNotFound(true);
                }
            })();
        } else {
            setNotFound(true);
        }
    }, [id]);

    return (!notFound ?
        <>
            <Helmet>
                {title &&
                    <title>{title} | LOGGIT</title>
                }
            </Helmet>
            <h1>{title}</h1>
            <div>{upload}</div>
            <hr />
            <ArticleDetail articleData={articleData} />
        </>
    :
        <></>
    );
}

export default Article;