import { useEffect, useState } from "react";
import CodeBlock from "./CodeBlock";
import ArticleLink from "./ArticleLink";
import Prism from "prismjs";

const ArticleDetail = ({articleData}) => {

    const [articleDom, setArticleDom] = useState([]);
    
    useEffect(() => {
        window.scrollTo(0, 0);
        if (!articleData) return;
        const lines = articleData.split('\n');
        (async () => {
            const doms = [];
            for (let i = 0; i < lines.length; i++) {
                const line = lines[i];
                if (line.indexOf('# ') === 0) {
                    doms.push(<h1>{line.substring(2)}</h1>);
                } else if (line.indexOf('## ') === 0) {
                    doms.push(<h2>{line.substring(3)}</h2>);
                } else if (line.indexOf('### ') === 0) {
                    doms.push(<h3>{line.substring(4)}</h3>);
                } else if (line.indexOf('#### ') === 0) {
                    doms.push(<h4>{line.substring(5)}</h4>);
                } else if (line.indexOf('##### ') === 0) {
                    doms.push(<h5>{line.substring(6)}</h5>);
                } else if (line.indexOf('###### ') === 0) {
                    doms.push(<h6>{line.substring(7)}</h6>);
                } else if (line.indexOf('- ') === 0) {
                    const list = [];
                    for ( ; i < lines.length && lines[i].indexOf('- ') === 0; i++) {
                        list.push(lines[i].substring(2));
                    }
                    i--;
                    doms.push(<ul>{list.map((li, key) => <li key={key}>{li}</li>)}</ul>);
                } else if (line.indexOf('| ') === 0) {
                    const head = line.substring(2);
                    const trs = [];
                    for ( i++; i < lines.length && lines[i].indexOf('|') !== 0; i++) {
                        trs.push(lines[i].split('|'));
                    }
                    doms.push(<table>{head.length > 0 && <thead><tr>{head.split('|').map((th,key)=><th key={key}>{th}</th>)}</tr></thead>}<tbody>{trs.map((tr,key)=><tr key={key}>{tr.map((td,key)=><td key={key}>{td}</td>)}</tr>)}</tbody></table>);
                } else if (line.indexOf('```') === 0) {
                    const props = line.substring(3).split(',');
                    const lang = props[0];
                    const file = props.length > 1 ? props[1] : null;
                    let code = '';
                    for ( i++; i < lines.length && lines[i].indexOf('```') !== 0; i++) {
                        if (code) code += '\n';
                        code += lines[i];
                    }
                    doms.push(<CodeBlock lang={lang} file={file} code={code} />);
                } else if (line.indexOf('~ ') === 0) {
                    const articleId = line.substring(2);
                    try {
                        const articles = await(await fetch('/articlelist.json')).json();
                        const filtered = articles.filter(article => article.id === Number(articleId));
                        if (filtered.length !== 0) {
                            const article = filtered[0];
                            doms.push(<ArticleLink article={article} />)
                        }
                    } catch (e) {
                    }
                } else {
                    doms.push(line);
                }
            }
            setArticleDom(doms);
        })();
    }, [articleData]);

    useEffect(() => {
        Prism.highlightAll();
    }, [articleDom]);

    return (
        <div>
            {articleDom.map((article, key) => 
                <div key={key}>{article}</div>
            )}
        </div>
    );
}

export default ArticleDetail;